$breakpoints-down: (
  "large":     "1399.98px", //1399.98px
  "extra-big": "1199.98px", //1199.98px
  "big":       "991.98px",  //991.98px
  "medium":    "767.98px",  //767.98px
  "regular":   "575.98px",  //575.98px
  "small":     "480.98px",  //480.98px
  "xsmall":    "375.98px",  //375.98px
);

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}

@mixin default {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(15px);
  border-radius: 15px;
}