.author {
  position: relative;
  &__title {
    margin-bottom: 7rem;
  }
  &__inner {
    display: flex;
    align-items: center;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 7rem 4.5rem;
    max-width: 64rem;
  }
  &__item {
    max-width: 29rem;
    width: 100%;
  }
  &__nums {
    font-size: var(--fz-56);
    font-weight: var(--fw-700);
    color: var(--clr-orange);
    padding-bottom: 1.5rem;
    margin-bottom: 1.5rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  }
  &__descr {
    font-size: var(--fz-24);
    font-weight: var(--fw-700);
  }
  &__img {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 74.5rem;
    object-fit: cover;
  }
}
