@use '../additional/mixins.scss' as *;
.faq {
  position: relative;
  &__title {
    text-align: center;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 8.5rem;
    margin-bottom: 5rem;
  }
  &__list {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    width: 100%;
  }
  &__item {
    display: flex;
    flex-direction: column;
    padding: 3rem 7rem 3rem 3rem;
    max-width: 68rem;
    width: 100%;
    @include default;
    font-weight: var(--fw-600);
    cursor: pointer;
    position: relative;

    &::before,
    &::after {
      content: '';
      position: absolute;
      background-color: var(--clr-orange);
      transition: .3s linear;
      border-radius: 100vmax;
    }

    &::before {
      top: 42px;
      right: 40px;
      width: 30px;
      height: 3px;
    }

    &::after {
      top: 28px;
      right: 54px;
      width: 3px;
      height: 30px;
    }

    &.active {
      &::after {
        transform: rotate(90deg) scale(0);
      }

      .faq__questions {
        margin-bottom: 1.5rem;
      }
      .faq__answer {
        height: 100%;
        opacity: 1;
      }
    }
  }
  &__questions {
    text-transform: uppercase;
    font-size: var(--fz-20);
    font-weight: var(--fw-600);
    transition: .3s linear;
  }
  &__answer {
    height: 0;
    opacity: 0;
    transition: .3s linear;
  }
  &__img {
    position: absolute;
    top: -10px;
    right: 0;
    object-fit: contain;
    max-width: 41.5rem;
  }
  &__btn {
    font-size: var(--fz-32);
    font-weight: var(--fw-600);
    margin-inline: auto;
    max-width: 44.5rem;
    padding: 1.7rem 2rem;
    background-color: var(--clr-orange);
  }
}
