.hero {
  padding: 0;
  position: relative;

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100vh;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -110px;
      z-index: var(--i-lower);
      background: url('../img/hero-img.png') no-repeat center / cover;
      width: 67rem;
      height: 88rem;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      background: url('../img/svg/icons/hero-icon.svg') no-repeat center / cover;
      width: 40px;
      height: 202px;
    }

  }
  &__title {
    max-width: 61rem;
    margin-bottom: 2rem;

    span {
      color: var(--clr-orange);
      font-style: italic;
    }
  }
  &__subtitle {
    font-family: var(--ff-2);
    text-transform: uppercase;
    font-size: var(--fz-28);
    letter-spacing: .2px;
    max-width: 42rem;
    color: var(--white);
    margin-bottom: 5rem;
    position: relative;
    z-index: var(--i-low);
  }

  &__btn {
    background-color: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(4px);
    font-family: var(--ff-2);
    color: var(--white);
    font-size: var(--fz-28);
    font-weight: var(--fw-500);
    padding: 1.8rem 4.5rem;
    max-width: 38rem;
    border-radius: 8px;
    text-transform: uppercase;
    overflow: hidden;

    &::before,
    &::after {
      content: '';
      position: absolute;
      background-color: var(--clr-orange);
      width: 30px;
      height: 30px;
      transform: rotate(45deg);
    }

    &::before {
      bottom: -15px;
      left: -15px;
    }

    &::after {
      top: -15px;
      right: -15px;
    }
  }
}
