@use '../additional/mixins.scss' as *;
.bonus {
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 2rem;
    margin-bottom: 12rem;
  }
  &__item {
    display: flex;
    align-items: center;
    flex-direction: column;
    max-width: 28rem;
    height: 330px;
    width: 100%;
    padding: 4rem 2.7rem 3rem;
    @include default;

    &:nth-child(even) {
     transform: translateY(45px);
    }

    svg {
      width: 56px;
      height: 56px;
    }
  }

  &__icon {
    width: 120px;
    height: 120px;
    border-radius: 100vmax;
    background-color: rgba(255, 255, 255, 0.1);
    border: 0.5px solid rgba(255, 255, 255, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__name {
    text-align: center;
    margin-bottom: 1.5rem;
    margin-top: auto;
  }
  &__text {
    text-align: center;
  }

  &__btn {
    background-color: var(--clr-orange);font-size: var(--fz-32);
    font-weight: var(--fw-600);
    max-width: 44.5rem;
    padding: 1.7rem 2rem;
    margin-inline: auto;
  }
}
