.result {
  &__list {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 4rem;
    padding: 2.5rem 3.5rem;
    width: 100%;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(15px);
    border-radius: 16px;
  }
  &__img {
    max-width: 14.2rem;
    object-fit: cover;
  }

  &__heading {
    max-width: 66.5rem;
    margin-bottom: 1.3rem;
    font-size: var(--fz-32);
    font-style: italic;
  }
  &__text {
    text-transform: uppercase;
    line-height: 1.3em;
  }
}
