@use '../additional/mixins.scss' as *;
.opportunities {
  &__title {
    text-align: center;
  }
  &__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 2rem;
  }
  &__item {
    padding: 3rem 2.5rem;
    max-width: 38rem;
    width: 100%;
    @include default;
  }

  &__icon {
    width: 100%;
  }
  &__heading {
    font-style: italic;
    margin-bottom: 1.5rem;
  }
  &__text {
    max-width: 29.5rem;
    margin-bottom: 3rem;
  }
}
