.footer {
  border-top: 1px solid #9e9e9e;
  padding: 5rem 0 8rem;
  color: var(--white);

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__wrapp {
    display: flex;
    flex-direction: column;
    font-weight: var(--fw-500);
    &:last-child {
      align-items: flex-end;
    }
  }
  &__name {
    margin-bottom: 4rem;
    font-size: var(--fz-40);
    font-weight: var(--fw-800);
  }
  &__info {
    margin-bottom: .8rem;
  }
  &__info-link {
    &:first-of-type {
      margin-right: 2.5rem;
    }

    &:hover {
      text-decoration: underline;
    }
  }
  &__list {
    margin-bottom: 3rem;
    display: flex;
    align-items: center;
    gap: 3rem;
  }
  &__item {
  }
  &__link {
    svg {
      width: 24px;
      height: 24px;
    }
  }
  &__phone {
    font-weight: var(--fw-500);
    margin-bottom: .8rem;
  }
  &__email {
  }
}

