.btn {
  display: block;
  width: 100%;
  border-radius: 16px;
  text-align: center;
  border: 1px solid transparent;
  cursor: pointer;
  transition: 0.1s linear;

  &:hover {
    background-color: var(--crl-black);
    color: var(--white);
    border: 1px solid #D9D9D9;
  }

  &:active {
    transform: scale(0.95);
  }
}
