@use '../additional/mixins.scss' as *;
.program {

  &__list {
    display: flex;
    flex-direction: column;
    gap: 4rem;
    margin-bottom: 7rem;
  }
  &__item {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;

    &:not(:last-child) {
      border-bottom: 2px dashed rgba(#fff, .2);
      padding-bottom: 4rem;
    }
  }

  &__heading {
    font-style: italic;
    text-transform: uppercase;
  }

  &__block {
    max-width: 58rem;
    width: 100%;

    &:first-child {
      min-width: 100%;
      max-width: 100%;
      padding: 2.5rem 5rem 2.5rem 6rem;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 3rem;
      flex-wrap: wrap;
      @include default;

      .program__heading {
        color: var(--clr-orange);
        text-shadow: 0px 0px 15px rgba(240, 176, 11, 0.4);
        margin-bottom: 1.5rem;
      }

      .program__descr {
        text-transform: uppercase;
        font-size: var(--fz-26);
        line-height: 1.35em;
        font-weight: var(--fw-700);
        max-width: 67rem;
        margin-bottom: 1.6rem;
      }

      svg {
        object-fit: contain;
        max-width: 24rem;
        width: 100%;
      }
    }

    &:nth-child(2) {
      background-color: var(--clr-orange);
      border-radius: 15px;
      padding: 3rem;

      .program__heading {
        margin-bottom: 1.5rem;
      }

      .program__text {
        font-size: var(--fz-18);
        font-weight: var(--fw-500);
        line-height: 1.3em;
      }
    }

    &:last-child {
      border: 1px solid var(--clr-orange);
      border-radius: 15px;
      padding: 4rem 3rem;

      .program__heading {
        margin-bottom: 2.5rem;
      }

      .program__bonuses {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 1.5rem;
      }

      .program__bonus {
        counter-increment: my-counter;
        max-width: 25rem;
        width: fit-content;
        font-weight: var(--fw-600);
        font-style: italic;
        align-self: center;
        line-height: 1.3em;
        align-self: center;
        padding-left: 3.5rem;
        position: relative;

        &::before {
          position: absolute;
          content: counter(my-counter);
          text-shadow: 0px 0px 10px var(--clr-orange);
          color: var(--clr-orange);
          @include default;
          display: flex;
          justify-content: center;
          align-items: center;
          margin-block: auto;
          top: 0;
          bottom: 0;
          left: 0;
          border-radius: 4px;
          width: 30px;
          height: 30px;
          padding: 6px;
        }
      }
    }
  }

  &__btn {
    margin-inline: auto;
    background-color: var(--clr-orange);
    text-transform: none;
    font-size: var(--fz-32);
    font-weight: var(--fw-600);
    max-width: 44.5rem;
    padding: 1.7rem 2rem;
  }
}
