html {
  font-size: var(--main-fz);
}

body {
  font-family: var(--ff-1);
  font-size: 1.6rem;
  font-weight: var(--fw-400);
  line-height: 1.2em;
  letter-spacing: .2px;
  color: var(--white);
  background-color: var(--clr-black);
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr min-content;
  align-content: start;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    inset: 0;
    z-index: -1;
    opacity: .8;
    background-image: url('../img/bg.png');
    background-repeat: repeat;
    background-size: cover;
    background-position: center;
  }
}

.container {
  max-width: var(--container);
  width: min(calc(100% - 30px), 1210px);
  margin-inline: auto;
}

.title {
  margin-bottom: 5.5rem;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.2em;
  font-weight: var(--fw-700);
  text-transform: uppercase;
  letter-spacing: .2px;
}

h1 {
  font-family: var(--ff-2);
  font-size: var(--fz-48);
  color: var(--white);
}

h2 {
  font-size: var(--fz-48);
  font-style: italic;
  color: var(--white);
}

h3 {
  font-size: var(--fz-24);
}

p {
  line-height: 1.2em;
}

span {
  display: block;
  line-height: 1.2em;
}

section {
  padding: 7.5rem 0;
}
