@use '../additional/mixins.scss' as *;
.advantages {
  &__title {
    margin-bottom: 2.5rem;
  }
  &__subtitle {
    max-width: 55rem;
    margin-bottom: 2.4rem;
    font-size: var(--fz-24);
    color: var(--white);
    opacity: .8;
  }

  &__text {
    font-family: var(--ff-2);
    font-size: var(--fz-32);
    font-weight: var(--fw-500);
    font-style: italic;
    color: var(--clr-orange);
    margin-bottom: 5.5rem;
    opacity: .8;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: -25px;
      left: 30px;
      background-color: var(--clr-orange);
      border-radius: 16px;
      width: 19.5rem;
      height: 66.8rem;
    }
  }
  &__item {
    @include default;
    backdrop-filter: blur(45px);
    display: flex;
    align-items: center;
    gap: 3.5rem;
    padding: 2.8rem;
    text-transform: uppercase;
    font-size: var(--fz-32);
    font-weight: var(--fw-700);
    line-height: 1.3em;

    svg {
      height: 78px;
      max-width: 120px;
      width: 100%;
    }
  }

}
